.wrap-container ul {
  padding: 10px;
  display: flex;
  flex-direction: row;

  justify-content: space-evenly;
  align-items: center;

  border-radius: 16px;
  margin: 10px;
  background-color: #55BDB3;
}

.wrap-container img {
  width: 80px;
}

.wrap-container button {
  background-color: #55BDB3;
  border-style: hidden;
  font-size: 1em;
  font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
}

/*

*/
@media (max-width:500px) {

  .wrap-container {
    width: 100%;
    background-color: blue;
    font-size: 8em;
  }

  #section_process,
  #figma iframe {
    width: 10%;

  }
}

@media (min-width:600px) {
  .wrap-container {
    width: 100%;
    background-color: blue;
    font-size: 0.5em;
  }

}

#test-text {
  padding: 10%
}



#section_name {
  color: #55BDB3;
}

section {
  justify-content: center;
  text-align: center;
}

#feedback {
  width: 80%;
}

#figma iframe {
  border: "4px solid rgba(0, 0, 0, 0.1)";
  width: 600px;
  height: 700px;

}

#contact img {
  width: 80px;
}

.break-word {
  padding-left: 10%;
  padding-right: 10%;
  text-align: justify;
}